.profile__wrapper {
  border-bottom: 1px solid #E8E8E8;
  min-width: 410px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  padding: 0 0 13px 0;
  margin: 0 0 20px 0;
}

.profile__wrapper:last-child {
  border-bottom: none;
}

@media screen and (max-width: 600px) {
  .profile__wrapper {
    min-width: 100%;
  }
}